import React from 'react'
import {Form as NativeForm, message, Modal, Table} from 'antd'
import {CommonSelect} from 'Form'
import {
  API_POST_CMS_OUTBOUND_ORDER_CONFIG_REPLACE,
  API_GET_CREATE_MANUAL_ORDER_SAP_CODE,
  API_GET_CMS_OUTBOUND_ORDER_CONFIG_BINDING_CUSTOMER
} from 'api'
import {getData} from 'util'
import {popupFormItemLayout} from 'enum'

class Madol extends React.Component {
  constructor(props) {
    super(props)

    this.state = {
      resetList: {}, // 重置下拉框options
      sapCusCodeListConfig: {api: API_GET_CREATE_MANUAL_ORDER_SAP_CODE}, // 客户
      selectedRowKeys: [], // 选中的队列下标
      selectedRows: [] // 选中的队列数据
    }

    this.columns = [
      {
        title: '子客户编码',
        dataIndex: 'sapSubCusCode',
        key: 'sapSubCusCode',
        align: 'center',
        width: 250
      },
      {
        title: '子客户名称',
        dataIndex: 'sapSubCusName',
        key: 'sapSubCusName',
        align: 'center',
        width: 250
      }
    ]
  }

  componentWillUnmount() {
    this.resetSelectedRowKeys()
  }

  /* 客户名称 查询 */
  handleSapCodeSearch = searchKey => {
    if (searchKey) {
      let searchParams = {
        size: 10,
        searchKey
      }
      this.setState({sapCusCodeListConfig: {api: API_GET_CREATE_MANUAL_ORDER_SAP_CODE, searchParams}})
    }
  }

  /* 客户名称 选择 */
  handleSapCusCodeSelected = value => {
    const {templateId} = this.props
    const params = {
      templateId,
      sapCusCode: value
    }
    getData({api: API_GET_CMS_OUTBOUND_ORDER_CONFIG_BINDING_CUSTOMER, searchParams: params}).then(res => {
      if (res && res.message === 'success') {
        let selectedRowKeys = []
        res.page.result.map(item => {
          item.key = item.sapSubCusCode
          if (item.binding) {
            selectedRowKeys.push(item.key)
          }
        })
        this.setState({
          selectedRowKeys,
          dataSource: res.page.result
        })
      }
    })
  }

  /* 确认按钮 */
  handleConfirm = () => {
    const {
      form: {validateFieldsAndScroll},
      handleCancelMadol,
      handleSapCus,
      handleReload,
      templateId
    } = this.props
    validateFieldsAndScroll((err, values) => {
      if (err) return
      try {
        const sapCusCode = values.sapCusCode || ''
        if (sapCusCode === '') return message.error('请输入必填内容')

        const {selectedRowKeys, dataSource} = this.state
        let relationItemList = []
        selectedRowKeys.map(item => {
          dataSource.map(child => {
            if (item === child.sapSubCusCode) {
              relationItemList.push({
                relationCode: child.sapSubCusCode,
                relationName: child.sapSubCusName
              })
            }
          })
        })

        let params = {
          sapCusCode,
          templateId,
          relationType: 1, // 1 ：客户类型
          relationItemList
        }
        getData({api: API_POST_CMS_OUTBOUND_ORDER_CONFIG_REPLACE, data: params}).then(data => {
          if (data && data.message == 'success') {
            handleReload()
            handleSapCus({id: templateId})
            handleCancelMadol()
            message.success('提交成功')
          }
        }).catch(err => {
          message.error('提交失败')
        })
      } catch (err) {
        console.error(err)
      }
    })
  }

  /*---------------- 表格操作 -----------------*/
  /* 更新选中的数据 */
  handleSelectChange = (selectedRowKeys, selectedRows) => this.setState({selectedRowKeys, selectedRows})

  /* 表格选中操作 */
  updateSelectedRows = (record, selected, selectedRows, nativeEvent) => {
    let arr = []
    selectedRows && selectedRows.map(item => arr.push(item.key))
    this.handleSelectChange([...new Set(arr)], selectedRows)
  }

  /* 重置选中的key */
  resetSelectedRowKeys = () => {
    this.setState({
      selectedRowKeys: [],
      selectedRows: []
    })
  }
  /*---------------- 表格操作 -----------------*/

  render() {
    const {
      form: {getFieldDecorator},
      handleCancelMadol
    } = this.props
    const {
      resetList,
      sapCusCodeListConfig,
      dataSource,
      selectedRowKeys
    } = this.state

    /* 选择功能的配置 */
    const rowSelection = {
      selectedRowKeys,
      onChange: this.handleSelectChange,
      onSelect: this.updateSelectedRows
    }

    return (
      <Modal
        visible={true}
        onOk={this.handleConfirm}
        onCancel={handleCancelMadol}
        title="客户绑定模版"
        okText="确认绑定"
      >
        <div className="sap-sub-customer-madol">
          <CommonSelect
            allowClear={false}
            required={true}
            getFieldDecorator={getFieldDecorator}
            label="客户"
            showSearch
            // mode="multiple"
            filterOption={false}
            decorator="sapCusCode"
            listConfig={sapCusCodeListConfig}
            onSearch={this.handleSapCodeSearch}
            onChange={this.handleSapCusCodeSelected}
            placeholder="请选择"
            listRenderGetValue={item => item.sapCusCode}
            listRenderGetLabel={item => `${item.sapCusCode} ${item.sapCusName}`}
            resetList={resetList}
            {...popupFormItemLayout}
          />
          <Table
            rowKey={record => record.sapSubCusCode}
            rowSelection={rowSelection}
            dataSource={dataSource}
            columns={this.columns}
            pagination={false}
            scroll={{x: true, y: 300}}
          />
        </div>
      </Modal>
    )
  }
}

export default NativeForm.create()(Madol)



