import React from 'react'
import {Button, Form as NativeForm, message, Modal, Row, Checkbox} from 'antd'
import {CommonFormItem, CommonInput, CommonSelect} from 'Form'
import {
  API_POST_CMS_OUTBOUND_ORDER_CREATE,
  API_PUT_CMS_OUTBOUND_ORDER_UPDATE,
  API_GET_CMS_OUTBOUND_ORDER_COLUMN_FIND
} from 'api'
import {getData} from 'util'
import {popupFormItemLayout} from 'enum'
import '../style/InventoryDcMadol.less'
import HeaderKeys from './HeaderKeys'

/* 头部 */
const headerList = [
  {
    code: 'H_OUTSTOCK_01',
    name: '默认版'
  }, {
    code: 'H_OUTSTOCK_02',
    name: '客户号版'
  }, {
    code: 'H_OUTSTOCK_03',
    name: '传真版'
  }, {
    code: 'H_OUTSTOCK_04',
    name: '分页版'
  }, {
    code: 'H_OUTSTOCK_05',
    name: '团购专用版'
  }, {
    code: 'H_OUTSTOCK_06',
    name: '重庆联系人版'
  }, {
    code: 'H_OUTSTOCK_07',
    name: '北京业务联系人版'
  }
]
/* 底部 */
const footerList = [
  {
    code: 'F_OUTSTOCK_01',
    name: '默认版'
  }, {
    code: 'F_OUTSTOCK_02',
    name: '审核人版'
  }, {
    code: 'F_OUTSTOCK_03',
    name: '送货人签字版'
  }, {
    code: 'F_OUTSTOCK_04',
    name: '备注版'
  }, {
    code: 'F_OUTSTOCK_05',
    name: '含税版'
  }, {
    code: 'F_OUTSTOCK_06',
    name: '经办人签字版'
  }, {
    code: 'F_OUTSTOCK_07',
    name: '分页汇总版'
  }, {
    code: 'F_OUTSTOCK_08',
    name: '团购专用版'
  }
]

class Madol extends React.PureComponent {
  constructor(props) {
    super(props)

    this.state = {
      resetList: {}, // 重置下拉框options
      columns: [], // 模板配置字段列表 - 下拉数据
      columnList: [] // 模板配置字段列表 - 选择数据
    }
  }

  componentWillMount() {
    const {form: {setFieldsValue}, templateInfo} = this.props

    if (templateInfo) {
      this.setState({
        kgToJin: templateInfo.kgToJin,
        columnList: templateInfo.columnList
      })
      setTimeout(() => {
        setFieldsValue({
          templateName: templateInfo.templateName,
          headerType: templateInfo.headerType,
          footerType: templateInfo.footerType
        })
      }, 200)
    }

    this._fetchColumnFind()
  }

  /* 获取模板配置字段列表 */
  _fetchColumnFind = () => {
    getData({api: API_GET_CMS_OUTBOUND_ORDER_COLUMN_FIND}).then(res => {
      if (res && res.message === 'success') {
        this.setState({
          columns: res.result
        })
      }
    })
  }

  /* 确认按钮 */
  handleConfirm = () => {
    const {
      form: {validateFieldsAndScroll},
      handleCancelMadol,
      handleReload,
      templateInfo
    } = this.props
    validateFieldsAndScroll((err, values) => {
      if (err) return
      const {columnList, kgToJin} = this.state
      try {
        let params = {
          templateName: values.templateName || '',
          headerType: values.headerType || '',
          footerType: values.footerType || '',
          columnList,
          kgToJin
        }

        if (params.templateName === '' || params.headerType === '' || params.footerType === '' || !params.columnList.length) return message.error('请输入必填内容')
        let api = API_POST_CMS_OUTBOUND_ORDER_CREATE
        if (templateInfo) {
          params.id = templateInfo.id
          api = API_PUT_CMS_OUTBOUND_ORDER_UPDATE
        }
        getData({api, data: params}).then(data => {
          if (data && data.message == 'success') {
            handleReload()
            handleCancelMadol()
            message.success(templateInfo ? '修改成功' : '提交成功')
          }
        }).catch(err => {
          console.error(err)
          message.error(templateInfo ? '修改失败' : '提交失败')
        })
      } catch (err) {
        console.error(err)
      }
    })
  }

  /* 添加 表头 */
  handleAddColumn = () => {
    this.setState({
      columnList: [...this.state.columnList, {}]
    })
  }

  /* 删除 表头 */
  handleDeleteColumn = index => {
    this.state.columnList.splice(index, 1)
    this.setState({
      columnList: [...this.state.columnList]
    })
  }

  /* 更新 表头 */
  handleUpdateColumn = (index, column) => {
    let list = [...this.state.columnList]
    list[index] = column
    this.setState({
      columnList: list
    })
  }

  /* 数据类型 改变 */
  handleCheckboxChange = (e) => {
    this.setState({
      kgToJin: e.target.checked
    })
  }

  render() {
    const {
      form: {getFieldDecorator},
      handleCancelMadol,
      templateInfo
    } = this.props
    const {
      resetList,
      columns,
      columnList,
      kgToJin
    } = this.state

    return (
      <Modal
        visible={true}
        onOk={this.handleConfirm}
        onCancel={handleCancelMadol}
        title={templateInfo ? '修改单据' : '新建单据'}
      >
        <div className="add-template-madol">
          <CommonInput
            getFieldDecorator={getFieldDecorator}
            label="名称"
            required={true}
            decorator="templateName"
            maxLength="20"
            placeholder="请输入(字数20内)"
            onChange={this.handleBlurTemplateName}
            {...popupFormItemLayout}
          />
          <CommonSelect
            getFieldDecorator={getFieldDecorator}
            label="头部"
            required={true}
            showSearch
            // mode="multiple"
            decorator="headerType"
            list={headerList}
            placeholder="请选择"
            listRenderGetValue={item => item.code}
            listRenderGetLabel={item => item.name}
            resetList={resetList}
            {...popupFormItemLayout}
          />
          <CommonSelect
            getFieldDecorator={getFieldDecorator}
            label="尾部"
            required={true}
            showSearch
            // mode="multiple"
            decorator="footerType"
            list={footerList}
            placeholder="请选择"
            listRenderGetValue={item => item.code}
            listRenderGetLabel={item => item.name}
            resetList={resetList}
            {...popupFormItemLayout}
          />
          <CommonFormItem
            label="数据类型"
            decorator="dataType"
            getFieldDecorator={getFieldDecorator}
            {...popupFormItemLayout}
          >
            <Checkbox checked={kgToJin} onChange={this.handleCheckboxChange}>千克转斤</Checkbox>
          </CommonFormItem>
          <CommonFormItem.Item
            label="表格头部字典"
            required={true}
            decorator="key"
            {...popupFormItemLayout}
          >
            <Row type="flex" justify="space-between">
              <span>英文字段</span>
              <span>中文名称</span>
              <Button shape="circle" icon="plus" title="添加表头" onClick={this.handleAddColumn} />
            </Row>
          </CommonFormItem.Item>
          {columnList.length ? <HeaderKeys
            columns={columns}
            columnList={columnList}
            handleDeleteColumn={this.handleDeleteColumn}
            handleUpdateColumn={this.handleUpdateColumn}
          /> : null}
        </div>
      </Modal>
    )
  }
}

export default NativeForm.create()(Madol)



