import React, {PureComponent} from 'react'
import {
  Button
} from 'antd'
import '../style/Tool.less'

export default class TableTool extends PureComponent {
  render() {
    const {
      handleAddModule
    } = this.props

    return (
      <div className="table-tool">
        <Button className="btn" type="primary" onClick={handleAddModule}>新建单据</Button>
      </div>
    )
  }
}



