import React, {PureComponent} from 'react'
import {
  Table
} from 'antd'
import moment from 'moment'
import '../style/Table.less'

export default class TableComponent extends PureComponent {
  constructor() {
    super()

    this.columns = [
      {
        dataIndex: 'id',
        key: 'id',
        title: '序号',
        width: 50,
        align: 'center'
      }, {
        title: ' 单据名称',
        dataIndex: 'templateName',
        key: 'templateName',
        width: 250,
        align: 'center'
      }, {
        title: '库存地点',
        dataIndex: 'relationDcCount',
        key: 'relationDcCount',
        width: 100,
        align: 'center',
        render: (text, record) => <a onClick={this.props.handleInventoryDc.bind(this, record)}>{text}</a>
      }, {
        title: '客户数',
        dataIndex: 'relationCustCount',
        key: 'relationCustCount',
        width: 100,
        align: 'center',
        render: (text, record) => <a onClick={this.props.handleSapCus.bind(this, record)}>{text}</a>
      }, {
        title: '最后更新时间',
        dataIndex: 'updatedTime',
        key: 'updatedTime',
        width: 125,
        align: 'center',
        render: (text, record, index) => moment(text).format('YYYY-MM-DD HH:mm:ss')
      }, {
        title: '操作',
        dataIndex: 'handle',
        key: 'handle',
        width: 150,
        align: 'center',
        render: (text, record) => (
          <span>
            <a className="btn-link" onClick={this.props.handleBrowse.bind(this, record)}>预览</a>
            <a className="btn-link" onClick={this.props.handleEditTemplate.bind(this, record)}>编辑</a>
            <a className="btn-link" onClick={this.props.handleDeleteTemplate.bind(this, record)}>删除</a>
          </span>
        )
      }
    ]
  }

  render() {
    const {dataSource, loading} = this.props
    return (
      <Table
        className="table-group"
        rowKey={record => record.id}
        loading={loading}
        columns={this.columns}
        dataSource={dataSource}
        pagination={false}
        bordered
        size="middle"
      />
    )
  }
}



