import React from 'react'
import {Row, Col, List, Button} from 'antd'
import {TableItemSelect, TableItemInput} from 'HOC'
import '../style/HeaderKeys.less'

export default class HeaderKeys extends React.Component {

  /* 字段 下拉框 */
  handleSelectChange = (index, value) => {
    const column = this.props.columnList[index]
    const arr = value.split(' ')
    column.field = arr[0]
    column.name = arr[1]
    this.props.handleUpdateColumn(index, column)
  }

  /* 中文名称 输入框 */
  handleInputChange = (index, e) => {
    let column = this.props.columnList[index]
    column.name = e.target.value
    this.props.handleUpdateColumn(index, column)
  }

  /* 删除 */
  handleDelete = (index) => typeof index != 'undefined' && this.props.handleDeleteColumn(index)

  render() {
    const {columnList, columns} = this.props
    return (
      <section>
        <List
          dataSource={columnList}
          renderItem={(item, index) => (
            <List.Item className="header-keys">
              <Row style={{width: '100%'}}>
                <Col span={6} className="label">
                  <label>{`表头(${index + 1})`}</label>
                </Col>
                <Col span={14}>
                  <Col span={10} className="mr10">
                    <TableItemSelect
                      allowClear={false}
                      showSearch
                      placeholder="字段"
                      list={columns || []}
                      listRenderGetValue={item => `${item.columnField} ${item.columnName}`}
                      listRenderGetLabel={item => `${item.columnField} ${item.columnName}`}
                      onChange={this.handleSelectChange.bind(this, index)}
                      value={item.field}
                    />
                  </Col>
                  <Col span={10}>
                    <TableItemInput
                      required={true}
                      placeholder="名称"
                      value={item.name}
                      onChange={this.handleInputChange.bind(this, index)}
                    />
                  </Col>
                  <Col className="minus">
                    <Button type="danger" shape="circle" icon="minus" title="删除" onClick={this.handleDelete.bind(this, index)}/>
                  </Col>
                </Col>
              </Row>
            </List.Item>
          )}
        />
      </section>
    )
  }
}



