import React from 'react'
import {Form as NativeForm, message, Modal} from 'antd'
import {CommonSelect} from 'Form'
import {
  API_POST_CMS_OUTBOUND_ORDER_CONFIG_REPLACE,
  API_GET_PROMOTION_DETAILS_INVENTORY_DCS
} from 'api'
import {getData} from 'util'
import {popupFormItemLayout} from 'enum'
import '../style/InventoryDcMadol.less'

class Madol extends React.PureComponent {
  constructor(props) {
    super(props)

    this.state = {
      resetList: {}, // 重置下拉框options
      inventoryDcList: [], // 库存数据
      inventoryDcConfig: {api: API_GET_PROMOTION_DETAILS_INVENTORY_DCS} // 库存地点
    }
  }

  componentWillMount() {
    const {inventoryDcList} = this.props

    if (inventoryDcList) {
      let list = inventoryDcList.map((item, index) => `${item.relationCode} ${item.relationName}`)
      this.setState({
        inventoryDcList: list
      })
    }
  }

  /* 确认按钮 */
  handleConfirm = () => {
    const {
      form: {validateFieldsAndScroll},
      handleCancelMadol,
      handleReload,
      templateId
    } = this.props
    validateFieldsAndScroll((err, values) => {
      if (err) return
      try {
        let relationItemList = []
        values.inventoryDc.map(item => {
          let arr = item.split(' ')
          relationItemList.push({
            relationCode: arr[0],
            relationName: arr[1]
          })
        })

        let params = {
          templateId,
          relationType: 0, // 0 ：库存类型
          relationItemList
        }
        getData({api: API_POST_CMS_OUTBOUND_ORDER_CONFIG_REPLACE, data: params}).then(data => {
          if (data && data.message == 'success') {
            handleReload()
            handleCancelMadol()
            message.success('提交成功')
          }
        }).catch(err => {
          message.error('提交失败')
        })
      } catch (err) {
        console.error(err)
      }
    })
  }

  render() {
    const {
      form: {getFieldDecorator},
      handleCancelMadol
    } = this.props
    const {
      inventoryDcConfig,
      resetList,
      inventoryDcList
    } = this.state

    return (
      <Modal
        visible={true}
        onOk={this.handleConfirm}
        onCancel={handleCancelMadol}
        title="选择库存地点"
      >
        <div className="inventory-dc-madol">
          <CommonSelect
            getFieldDecorator={getFieldDecorator}
            label="库存地点"
            // showSearch
            mode="multiple"
            decorator="inventoryDc"
            listConfig={inventoryDcConfig}
            placeholder="请选择"
            listRenderGetValue={item => `${item.dcCode} ${item.dcName}`}
            listRenderGetLabel={item => `${item.dcCode} ${item.dcName}`}
            resetList={resetList}
            initialValue={inventoryDcList}
            {...popupFormItemLayout}
          />
        </div>
      </Modal>
    )
  }
}

export default NativeForm.create()(Madol)



