import React from 'react'
import {Bread} from 'Bread'
import {getData, switchUrlParam} from 'util'
import {
  Tool,
  AddModuleTemplate,
  TableComponent,
  InventoryDcMadol,
  SapSubCustomerMadol
} from './components'
import {
  API_GET_CMS_OUTBOUND_ORDER_CONFIG_SEARCH,
  API_GET_CMS_OUTBOUND_ORDER_CONFIG_INVENTORY_DC,
  API_GET_CMS_OUTBOUND_ORDER_CONFIG_RELATED_CUSTOMER,
  API_DELETE_CMS_OUTBOUND_ORDER_CONFIG_DELETE
} from 'api'
import './style/index.less'
import {Popup} from 'HOC'
import {Button, Table} from 'antd'

export default class App extends React.Component {
  constructor() {
    super()

    this.state = {
      isShowInventoryDc: false, // 是否显示库存地点
      templateId: null, // 模版ID
      inventoryDcList: [], // 库存数据
      isShowSapSubCustomer: false, // 是否显示子客户
      isShowAddTemplate: false, // 是否显示新增模版
      dataSource: [], // 数据源
      templateInfo: null, // 模版信息
      loading: false, // 列表loading
      warningTipsMessages: null, // 警告提示 信息
      visible: false, // 是否显示弹出框
      type: '0' // 弹框类型
    }

    this.columns = [
      {
        title: '客户编码',
        dataIndex: 'sapCusCode',
        key: 'sapCusCode',
        align: 'center',
        width: 250
      },
      {
        title: '子客户编码',
        dataIndex: 'sapSubCusCode',
        key: 'sapSubCusCode',
        align: 'center',
        width: 250
      },
      {
        title: '子客户名称',
        dataIndex: 'sapSubCusName',
        key: 'sapSubCusName',
        align: 'center',
        width: 250
      }
    ]
  }

  componentWillMount() {
    this._fetchOutboundOrder()
  }

  /* 请求出库单模版数据 */
  _fetchOutboundOrder = () => {
    getData({api: API_GET_CMS_OUTBOUND_ORDER_CONFIG_SEARCH}).then((res) => {
      if (res && res.message === 'success') {
        this.setState({
          dataSource: res.page.result
        })
      }
    })
  }

  /* 库存地点 */
  handleInventoryDc = (record) => {
    const params = {
      templateId: record.id,
      relationType: 0 // 0 ：库存类型
    }
    getData({
      api: API_GET_CMS_OUTBOUND_ORDER_CONFIG_INVENTORY_DC,
      searchParams: params
    }).then((res) => {
      if (res && res.message === 'success') {
        this.setState({
          isShowInventoryDc: true,
          templateId: record.id,
          inventoryDcList: res.page.result
        })
      }
    })
  }

  /* 客户数 */
  handleSapCus = (record) => {
    const params = {
      templateId: record.id
    }
    getData({
      api: API_GET_CMS_OUTBOUND_ORDER_CONFIG_RELATED_CUSTOMER,
      searchParams: params
    }).then((res) => {
      if (res && res.message === 'success') {
        let warningTipsMessages = []
        if (res.page.result && res.page.result.length) {
          res.page.result.map((item, index) => {
            item.key = index
            warningTipsMessages.push(item)
          })
        }
        this.setState({
          templateId: record.id,
          visible: true,
          type: '1',
          warningTipsMessages
        })
      }
    })
  }

  /* 预览 */
  handleBrowse = ({id, pageSize}) => {
    const params = {
      templateId: id,
      pagination: pageSize > 0
    }
    const url = switchUrlParam('/wms/outboundOrderTemplate', params)
    window.open(
      url,
      'newWindow',
      'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no'
    )
  }

  /* 编辑 */
  handleEditTemplate = (record) => {
    this.setState({
      isShowAddTemplate: true,
      templateInfo: record
    })
  }

  /* 删除 */
  handleDeleteTemplate = (record) => {
    this.setState({
      templateId: record.id,
      visible: true,
      type: '2'
    })
  }

  /* 确认删除 */
  handleDeleteOk = () => {
    const {templateId} = this.state
    getData({
      api: API_DELETE_CMS_OUTBOUND_ORDER_CONFIG_DELETE,
      urlParams: [templateId]
    }).then((res) => {
      if (res && res.message === 'success') {
        this.handleCancel()
        this.handleReload()
      }
    })
  }

  /* 新增子客户 */
  handleEditCustomer = () => {
    this.setState({
      isShowSapSubCustomer: true
    })
  }

  /* 重新加载 */
  handleReload = () => {
    this._fetchOutboundOrder()
  }

  /* 关闭弹框 */
  handleCancelMadol = () => {
    this.setState({
      isShowInventoryDc: false,
      isShowSapSubCustomer: false,
      isShowAddTemplate: false
    })
  }

  /* 新建模版 */
  handleAddModule = () => {
    this.setState({
      templateInfo: null,
      isShowAddTemplate: true
    })
  }

  /*--------- 弹出框 ---------- */
  /* 取消按钮 */
  handleCancel = () => {
    this.setState({visible: false, type: ''})
  }

  /* 提示框类型选择 */
  switchType = (value) => {
    if (value === '1') {
      return (
        <Popup
          title="客户数"
          visible={this.state.visible}
          onCancel={this.handleCancel}
          footer={
            <Button key="back" type="primary" onClick={this.handleEditCustomer}>
              修改
            </Button>
          }
          content={
            <Table
              style={{margin: '-24px'}}
              dataSource={this.state.warningTipsMessages}
              columns={this.columns}
              pagination={false}
              scroll={{x: true, y: 300}}
            />
          }
        />
      )
    } else if (value === '2') {
      return (
        // 取消订单
        <Popup
          visible={this.state.visible}
          okType={'danger'}
          onOk={this.handleDeleteOk}
          onCancel={this.handleCancel}
          content={<p style={{textIndent: '40px'}}>是否确认删除该模版?</p>}
        />
      )
    }
  }
  /*--------- 弹出框 ---------- */

  render() {
    const {
      isShowInventoryDc,
      templateId,
      inventoryDcList,
      isShowSapSubCustomer,
      isShowAddTemplate,
      dataSource,
      templateInfo,
      loading
    } = this.state

    return (
      <section>
        <Bread route={['配置信息', '大宗配置', '出库单配置']} />
        <Tool handleAddModule={this.handleAddModule} />
        <TableComponent
          handleInventoryDc={this.handleInventoryDc}
          handleSapCus={this.handleSapCus}
          handleBrowse={this.handleBrowse}
          handleEditTemplate={this.handleEditTemplate}
          handleDeleteTemplate={this.handleDeleteTemplate}
          dataSource={dataSource}
          loading={loading}
        />
        {isShowInventoryDc && (
          <InventoryDcMadol
            handleReload={this.handleReload}
            handleCancelMadol={this.handleCancelMadol}
            templateId={templateId}
            inventoryDcList={inventoryDcList}
          />
        )}
        {isShowSapSubCustomer && (
          <SapSubCustomerMadol
            handleReload={this.handleReload}
            handleSapCus={this.handleSapCus}
            handleCancelMadol={this.handleCancelMadol}
            templateId={templateId}
          />
        )}
        {isShowAddTemplate && (
          <AddModuleTemplate
            templateInfo={templateInfo}
            handleReload={this.handleReload}
            handleCancelMadol={this.handleCancelMadol}
          />
        )}
        {this.state.visible &&
          this.state.type &&
          this.switchType(this.state.type)}
      </section>
    )
  }
}



